import { useNavigate } from 'react-router-dom';

const PlayerCard = ({ player }) => {
  const navigate = useNavigate();

  const handleClick = () => {
    navigate(`/players/${player.name.toLowerCase().replace(/\s+/g, '-')}`, {
      state: { playerData: player }
    });
  };

  return (
    <div 
      className="bg-white rounded-lg shadow-sm hover:shadow-md transition-shadow overflow-hidden group cursor-pointer"
      onClick={handleClick}
    >
      <div className="aspect-[3/4] relative">
        <img
          src={player.img || '/api/placeholder/300/400'}
          alt={player.name}
          className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
        />
        <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/70 to-transparent p-4">
          <div className="flex justify-between items-end">
            <div>
              <h3 className="text-white font-bold leading-tight">{player.name}</h3>
              <p className="text-gray-200 text-sm">{player.grade}</p>
            </div>
            <span className="bg-white text-[#003057] px-2 py-1 rounded-full text-sm font-bold">
              #{player.number}
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};

